.header {
  scroll-snap-align: start;
  position: sticky;
  top: 0;
  display: flex;
  justify-content: space-between;
  gap: 0.5rem;
  max-height: 6rem;
  background-color: var(--theme-color);

  border-bottom-left-radius: 0.5rem;
  border-bottom-right-radius: 0.5rem;
  padding: 0.5rem;
}
.imgBox {
  flex: 1;
  border-radius: 0.25rem;
  overflow: hidden;
  background-color: var(--theme-color-alpha-100);
  max-width: 6rem;
  margin: -0.5rem;

  img {
    width: 100%;
  }
}
.infoBox {
  flex: 1;
  max-width: 8rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.name {
  color: black;
  padding: 0.5rem;
  border-radius: 0.25rem;
}
[data-element='응결'] {
  .name {
    background-color: var(--element-응결);
  }
}
[data-element='용융'] {
  .name {
    background-color: var(--element-용융);
  }
}
[data-element='전도'] {
  .name {
    background-color: var(--element-전도);
  }
}
[data-element='기류'] {
  .name {
    background-color: var(--element-기류);
  }
}
[data-element='회절'] {
  .name {
    background-color: var(--element-회절);
  }
}
[data-element='인멸'] {
  .name {
    background-color: var(--element-인멸);
  }
}

.levelBox {
  background-color: black;
  border-radius: 0.25rem;
  display: flex;
  font-size: 1rem;
  color: var(--theme-color);
  font-weight: bolder;

  span {
    padding: 0.5rem 0;
    width: 3rem;
    text-align: right;
  }
  select {
    font-weight: bolder;
    width: 100%;

    color: var(--theme-color);
    font-size: 1rem;

    border-radius: 0.25rem;
    background-color: black;
  }
}
.detail {
  max-width: 60rem;
  margin: auto;
  padding: 0.5rem 0;
  display: grid;
  grid-auto-flow: column;
  gap: 1rem;

  grid-template-columns: repeat(1, 1fr);
  grid-template-rows: repeat(6, 1fr);

  @media (min-width: 40rem) {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(3, 1fr);
  }
  @media (min-width: 60rem) {
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(2, 1fr);
  }

  section {
    scroll-snap-align: start;
    scroll-margin-top: 6rem;
    width: 100%;
    background-color: var(--theme-color-alpha-100);
    border-radius: 0.5rem;
    padding: 0.25rem;
  }
}
.weaponImgBox {
  align-self: flex-end;
  border-radius: 0.25rem;
  overflow: hidden;
  background-color: var(--theme-color-alpha-100);
  width: 5rem;
  max-height: 5rem;
  img {
    width: 100%;
  }
}

.skill {
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}
.skillNodes {
  display: flex;
  justify-content: space-between;
}
.skillLine {
  background-color: var(--theme-color-alpha-100);
  display: flex;
  flex-direction: column-reverse;
  justify-content: space-between;
  padding: 0.25rem;
  border-radius: 0.5rem;
  gap: 0.25rem;
}
.skillNode {
  height: 3rem;
  width: 3rem;
  border-radius: 100%;

  display: flex;
  justify-content: center;
  align-items: center;

  background-color: var(--theme-color-alpha-100);
  select {
    border: 10px solid transparent;
    background-color: var(--theme-color-alpha-100);
    height: 3rem;
    width: 3rem;
    border-radius: 100%;
    font-size: 1.5rem;
    background-color: var(--theme-color);
    color: black;
  }
}
.skillTags {
  display: flex;
  justify-content: space-between;
  span {
    width: 100%;
    line-height: 1.2;
    font-size: 0.75rem;
  }
}
.skillNode[data-selected='true'] {
  background-color: var(--theme-color-alpha-700);
}
